<template>
  <div class="list">
    <div class="list-d">{{list[$route.params.id].title}}</div>
    <!--<div class="list-d" v-for="(item,index) in list2">{{item.AddrName}}</div>-->
  </div>
</template>

<script>
import VueRouter from 'vue-router'
import m from './all.js'
export default {
  name: 'List',
  data() {
    return {
	  list:[
	    {img:"",title:"1",text:""},
		{img:"",title:"2",text:""},
		{img:"",title:"3",text:""},
		{img:"",title:"4",text:""},
		{img:"",title:"5",text:""},
		{img:"",title:"6",text:""},
	    {img:"",title:"7",text:""},
		{img:"",title:"8",text:""},
		{img:"",title:"9",text:""},
		{img:"",title:"10",text:""},
		{img:"",title:"11",text:""},
		{img:"",title:"12",text:""},
	  ],
	  list2:""
	}
  },
  mounted:async function() {
	//await m.delay(1000)
	alert(this.$route.params.id)
  },
  watch:{
    $route (to, from){
      alert(this.$route.params.id)
    }
  },
  methods:{
    f1:function(x) {
	  this.list2=this.list[x]
	},
	show_alert:function(x) {
	  var text=x||this.list2||null
	  alert(text)
	}
  }
}
</script>



