<template>
  <div class="left" :class="{'left-open':GLOBAL.left_open}">
	<input type="file" ref="file1" name="avatar" accept="application/vnd.openxmlformats-officedocument.spreadsheetml.sheet, application/vnd.ms-excel" @change="import_data($event,Function.import_data_driver)" style="display:none;" /> 
      <div class="left-d">
	    <div class="left-d-d" v-for="(item,index) in list" @click="change_list(index)">
		  <i class="left-d-d-icon" :class="item.img"></i>
		  <div class="left-d-d-d">{{item.title}}</div>
	    </div>
	    <div class="left-d-d"><router-link to="/Left">Go to Left</router-link></div>
	    <div class="left-d-d" @click="show_note">call alert</div>
      </div>
	  <div class="left-d-more" :class="{'left-d-more-show':GLOBAL.left_more_show}">
	  <router-link v-for="(item,index) in list[c_list].c_title" :to="item.url" v-if="item.url">
	    <div class="left-d-more-d">{{item.title}}</div>
	  </router-link>
	  <div class="left-d-more-d" @click="$refs[item.open_input].click()" v-else>{{item.title}}</div>
	  <div class="left-d-more-d" @click="GLOBAL.left_more_show=!GLOBAL.left_more_show"></div>
	</div>
  </div>
</template>

<script>
import moment from 'moment'
import m from './all.js'
export default {
  data() {
    return {
	  c_list:0,
	  list:[
	    {title:"",url:"/CaseList/",img:"el-icon-data-analysis",no:1,c_title:[
		  {title:"",url:"/Chart1",img:"",no:2},
		  {title:"",url:"/Chart2",img:"",no:3},
		  {title:"",img:"",url:"/Chart3",no:4},
		  {title:"",url:"/Chart4",img:"",no:5},
		  {title:"",url:"/Chart5",img:"",no:6},
		  {title:"",url:"/Chart6",img:"",no:7},
		  {title:"",url:"/Chart7",img:"",no:8},
		]},
		{title:"",url:"/CaseList/",img:"el-icon-document",no:9,c_title:[
		  {title:"",url:"/OrderList/1",img:"",no:10},
		  {title:"",url:"/CaseList/",img:"",no:11},
		  {title:"",url:"/CaseList/",img:"",no:12},
		  {title:"",url:"/CaseList/",img:"",no:13},
		  {title:"",url:"/CaseList/",img:"",no:14},
		]},
		{title:"",url:"/CaseList/",img:"el-icon-truck",no:15,c_title:[
		  {title:"",url:"/CaseList/",img:"",no:16},
		  {title:"",url:"/DragDispatch/",img:"",no:17},
		  {title:"-",url:"/TableDispatch/",img:"",no:18},
		]},
		{title:"",url:"/CaseList/",img:"el-icon-user",no:19,c_title:[
		  {title:"",url:"/CaseList/1?key=&pMode=1",img:"",no:20},
		  {title:"",url:"/CaseList/",img:"",no:21},
		  {title:"",url:"/CaseList/",img:"",no:22},
		  {title:"",url:"/CaseList/",img:"",no:23},
		]},
		{title:"",url:"/DriverList/",img:"el-icon-date",no:24,c_title:[
		  {title:"",url:"/DriverList/1?key=&status=",img:"",no:25},
		  {title:"",url:"/CarList/1?key=&Status=&carType=0",img:"",no:28},
		]},
		{title:"",url:"/CaseList/",img:"el-icon-setting",no:31,c_title:[
		  {title:"",url:"/CaseList/",img:"",no:32},
		  {title:"",url:"/CarsTypeList/",img:"",no:33},
		  {title:"",url:"/AddrTurnNameList/1",img:"",no:34},
		  {title:"",url:"/AdminList/1",img:"",no:35},
		]},
		{title:"",url:false,img:"el-icon-upload2",no:31,c_title:[
		  {title:"/",url:false,img:"",no:33,open_input:"file1"},
		]},
		{title:"",url:false,img:"el-icon-download",no:31,c_title:[
		  {title:"",url:false,img:"",no:32},
		  {title:"",url:false,img:"",no:33},
		  {title:"",url:false,img:"",no:34},
		]},
	  ]
	}
  },
  mounted:async function() {
    console.log(this.$route)
  },
  watch:{
    async $route(to, from){
	  this.GLOBAL.left_open=false
	  this.GLOBAL.left_more_show=false
    }
  },
  methods:{
    get_path:function(x) {
	  var arr=x.split("/")
	  console.log(arr)
	  return arr
	},
	change_list:function(x) {
	  this.GLOBAL.left_more_show=true
	  this.c_list=x;
	},
    reload:function() {
	  m.reload()
	},
	show_note:function() {
	  var data=moment().format('HH:mm:ss')
      this.$notify({
        title: '',
        dangerouslyUseHTMLString: true,
		duration:10000,
        message: '<div">:TS5555123<br>'+data+'</div>'
      });
	},
	import_data:async function(e,callback) {
	  this.GLOBAL.show_black=true
      let file=e.target.files[0]
      let formData = new FormData();
      formData.append('file', file);
	  alert(await callback(formData))
	  this.GLOBAL.show_black=false
	}
  }
}
</script>


